.not-found {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3em;
}

.not-found__header {
    font-size: 5em;
    text-align: center;
}

.not-found__link {
    text-decoration: none;
    font-size: 3em;
}

.not-found__link:hover {
    cursor: pointer;
    background: var(--rainbow-color, rgb(251, 63, 63));
    -webkit-background-clip: text;
    -webkit-text-stroke: 2px transparent;
    color: #fff;
}
